import __var_0__ from '@stdlib/stats/incr/count';
import __var_1__ from '@stdlib/stats/incr/max';
import __var_2__ from '@stdlib/stats/incr/min';
import __var_3__ from '@stdlib/stats/incr/range';
import __var_4__ from '@stdlib/stats/incr/midrange';
import __var_5__ from '@stdlib/stats/incr/sum';
import __var_6__ from '@stdlib/stats/incr/mean';
import __var_7__ from '@stdlib/stats/incr/variance';
import __var_8__ from '@stdlib/stats/incr/stdev';
import __var_9__ from '@stdlib/stats/incr/skewness';
import __var_10__ from '@stdlib/stats/incr/kurtosis';
